*{
  margin: 0;
  padding: 0;
}
ul{
  list-style: none;
  padding: 0;
}
a{
  text-decoration: none;
  color: #000000;
}
a:hover{
  text-decoration: underline;
}
body {
  padding-top: 50px;
  color: #000000;
}

.main-content {
  padding: 40px 15px;
}
.nav {
  display: flex;
}
.nav li {
  margin: 0 20px;
  white-space: nowrap;
}
.nav li.active>a{
  color: blue;
}

.select-nav{
  position: relative;
}
.select-nav:hover .posts-nav{
  display: block;
}
.posts-nav{
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  min-width: 100%;
}
.posts-nav li{
  margin: 0;
  padding: 5px 0;
}


.main-banner {
  /*background-size: cover;*/
  /*background-position: top center;*/
  /*background-repeat: no-repeat;*/
  /*background-image: url(../img/hero/1.jpg);*/
  background: #8c8c8c;
  text-align: center;
  position: relative;
  color: #ffffff;
}
.main-baner__welcome-text {
  font-size: 18px;
  font-weight: 500;
}
.main-baner__chain {
  line-height: 1.25;
  font-weight: 500;
  max-width: 390px;
  margin: 0 auto;
}
.main-baner__tel-text {
  font-size: 18px;
  font-weight: 500;
}
.main-baner__tel {
  font-size: 20px;
  color: #f8f8f8;
  font-weight: 700;
}